import React from 'react';
import {Form} from 'antd';
import 'antd/dist/antd.css';

export function FormGroup({name, label, required, ...props}) {
    return (
        <Form.Item
        name={name}
        label={<> {label}  </>}
        rules={ required? [
          {
            required: {required},
            message: 'This field is required',
          },
        ] : []}
        initialValue={props.initialValue}
      >
           {props.children}
        </Form.Item>
    )
}
