import { useQuery } from "react-query";
import { instance } from "../api/Api";

export function useDebitTransactions() {
  return useQuery("debitTransactions", async () => {
    const { data } = await instance.get("transactions/debit");
    return data;
  });
}

export function useCreditTransactions() {
  return useQuery("creditTransactions", async () => {
    const { data } = await instance.get("transactions/credit");
    return data;
  });
}

export function useOneTransactions(transactionId) {
    return useQuery(transactionId, async () => {
      const { data } = await instance.get("transactions/details/"+transactionId);
      return data;
    });
  }
